.social-links {
  display: flex;
  width: 181px;
  height: 50px;
  justify-content: space-between;
}
.social-links__item {
  width: 47px;
  border: 3px solid #e9ebec;
  border-radius: 50%;
}
.social-links__item--tw {
  background: url("../img/icon-twitter.svg") no-repeat 50%;
}
.social-links__item--fb {
  background: url("../img/icon-facebook.svg") no-repeat 50%;
}
.social-links__item--video {
  background: url("../img/icon-youtube.svg") no-repeat 50%;
}
.content {
  background-image: url("../img/bg-back-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: 50% 23%;
  background-color: #1d2631;
  background-size: 322px;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .content {
    background: url("../img/bg-back-mobile@2x.jpg");
  }
}
.content__photo {
  margin-top: 326px;
}
.content__title {
  color: #ffffff;
  font-size: 24px;
  line-height: 28px;
  font-weight: 300;
  text-align: center;
  padding-top: 31px;
  max-width: 280px;
  margin: 0 auto;
}
@media (min-width: 660px) {
  .content {
    background: url("../img/bg-back-tablet@1x.jpg") no-repeat 50% 28%;
    background-size: 1203px;
  }
  .content__photo {
    margin-top: 366px;
  }
  .content__title {
    font-size: 50px;
    line-height: 60px;
    padding-top: 69px;
    max-width: 620px;
  }
}
@media (min-width: 660px) and (min-resolution: 144dpi), (min-width: 660px) and (min-resolution: 1.5dppx) {
  .content {
    background: url("../img/bg-back-tablet@2x.jpg");
  }
}
@media (min-width: 960px) {
  .content {
    background: url("../img/bg-back-desktop@1x.jpg") no-repeat 45% 0%;
  }
  .content__photo {
    margin: 0;
  }
  .content__title {
    padding-top: 63px;
    max-width: 750px;
  }
}
@media (min-width: 960px) and (min-resolution: 144dpi), (min-width: 960px) and (min-resolution: 1.5dppx) {
  .content {
    background: url("../img/bg-back-desktop@2x.jpg");
  }
}
